.additional-item__item-name {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
  margin: 0;
}

.additional-item__info {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  margin-top: 13px;
}

.additional-item__order {
  margin-top: 15px;
}

@media (min-width: $tablet-width) {
  .additional-item {
    display: flex;
    flex-direction: column;
  }

  .additional-item__item-name {
    font-size: 20px;
    line-height: 20px;
  }

  .additional-item__info {
    font-size: 16px;
    line-height: 20px;
  }
}
