.product-information {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
}

.product-information__type {
  margin: 0;
}

.product-information__value {
  margin: 0;
}

@media (min-width: $tablet-width) {
  .product-information {
    font-size: 16px;
    line-height: 16px;
  }
}
